import React from "react";
import './SearchBox.css'

const SearchBox = ({ searchfield, searchChange }) => {
    return (
        <div className="pa2">
            <input
                className="pa3 ba b--blue bg-lightest-blue"
                type="search"
                placeholder="Search Robots"
                onChange={searchChange}
            />
        </div>
    )
}

export default SearchBox;